import React from 'react';
import logo from '../logo.png';


class Orr extends React.Component {
    render() {
        return (
<p>
  <font size='30' color ='black' face='Georgia, Arial'>  <h2>Pater sive natura prima !</h2></font>

<p align='top'><h3>Para a todos aquellos que se han creido varados en la tierra de los muertos,
 para todos aquellos que han regresado y han traido consigo todo una vida. Para todos los que han
 llevado lo absoluto del goce, a su mas alta desterritorializacion.</h3> 
</p>
<img src={logo} className="App-logo" alt="logo" />
</p>


        );
    }}


export default Orr;  